import React, { useEffect, useState } from "react";
import { Button, Container, Row, Table } from "reactstrap";
import { Repe } from "../Repetition/Repe";

export const Serie = ({ name, reps, edit }) => {
  const [serieName, setSerieName] = useState(name);
  const [repes, setRepes] = useState(Array.from(Array(reps).keys()))
  const [isEditable, setIsEditable] = useState(edit)

  useEffect(() => {
    console.log(reps)
    setIsEditable(edit)
    setSerieName(name)
    setRepes(Array.from(Array(reps).keys()))
  },[edit, name, reps]);

  const handleOnSave = () => {
    alert("Saved!!")
  }

  return (
    <Container>
      <Row><h4>{ serieName }</h4></Row>
     
      <Table>
        <thead>
              <tr>
                <th>#</th>
                <th>#repetitions</th>
                <th>weight</th>
              </tr>
            </thead>
        <tbody>
          {repes.map( (r) => (<Repe key={r} id={r} pNum={3} pWeigth={50} edit={ isEditable }></Repe>))}
        </tbody>
      </Table>
      <Button onClick={handleOnSave}>Save</Button>
    </Container>
  );
};